html, body {
height: 100%;
width: 100%;
background: #f7c8f8;
}

.root{
  height: 100%;
  width: 100%;
}

.container{
  background-color: #aaa;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  overflow: hidden;
  position: relative;
  left: 290px;
  top: 270px;
  padding: 20px;
}

.container__img{
  position: absolute;
  left: 290px;
  top: 270px;
  width: 100%;

}

.container__content {
  width: 60%;
  display: flex;
  flex-direction: column;
  padding: 100px;
}

.content__redessociais {
  display:flex;
  list-style: none;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.content__redessociais__item {
  margin-right: 10px;
}

.content__name{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: rgb(167, 14, 154);
  font-size: 46px;
  justify-content: center;
}
.content__description{
  padding: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  justify-content: center;


}